html[data-theme='light'] {
  --bg: #fff;
  --color-text: #333333;
  --textLight: #6C6C6C;
  --primary: #00A6A4;
  --primaryDisable: #00A6A455;
  --brandSecondary: #294C88;
  --divider: #E5E5E5;
  --placeholder: #BABABA;
  --menu: #fafafa;
  --cardColor: #fff;
  --green: #00A6A4;
  --red: #ff0000;
  --slideCard: #F2FBFB;
  --icon-color: #999999;
}

html[data-theme='dark'] {
  --bg: #001516;
  --color-text: #EFFFFF;
  --textLight: #CCCCCC;
  --primary: #13ABB1;
  --primaryDisable: #13ABB155;
  --brandSecondary: #7F93D3;
  --divider: #666B6B;
  --placeholder: #CCCCCC;
  --menu: #1f1f1f;
  --cardColor: #1d1d1d;
  --green: #00EBB1;
  --red: #F26B55;
  --slideCard: #1d1d1d;
  --icon-color: #999999;
}

h1, h2, h3, h4, h5, h6, p {
  color: var(--color-text);
}

.icon-def {
  color: var(--icon-color);
}

.progress {
  color: #001516;
  background-color: #00A6A4;
}

.progress-bar {
  color: red;
  background-color: #00A6A4;
}

.container {
  align-content: 'center';
  display: flex;
  flex: 1;
  flex-direction: column;
  padding-right: 15px;
  padding-left: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.containerData {
  align-content: 'center';
  display: flex;
  flex: 1;
  flex-direction: column;
  padding-right: 15px;
  padding-left: 15px;
  padding-top: 65px;
  padding-bottom: 65px;
}

.copycontainer {
  align-content: 'center';
  flex: 1;
  padding-bottom: 5px;
}

.primary {
  color: var(--primary);
}

.place-holder {
  color: var(--placeholder);
}

.textHeadauth {
  font-size: 18px;
  font-family: 'DMSans-Regular';
  font-weight: 400;
  margin-top: 40px;
  margin-bottom: 30px;
  color: var(--color-text);
}

.textHead {
  font-size: 18px;
  font-family: 'DMSans-Regular';
  font-weight: 400;
  /* margin-top: 40px; */
  margin-bottom: 20px;
  color: var(--color-text);
}

.textNormal {
  font-family: 'DMSans-Regular';
  font-size: 14px;
  color: var(--color-text);
}

.textHeader {
  font-family: 'DMSans-Regular';
  font-size: 18px;
  color: var(--color-text);
}

.textHelper {
  font-family: 'DMSans-Regular';
  font-size: 12px;
  color: var(--color-text);
}

.textNav {
  font-family: 'DMSans-Regular';
  font-size: 10px;
  color: var(--color-text);
}

.filename__base {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

/* nav top */

.navTop {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-right: 15px;
  padding-left: 15px;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 10px;
  position: fixed;
  width: 100%;
  background-color: var(--bg);
}

.navTopActive {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-right: 15px;
  padding-left: 15px;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 10px;
  position: fixed;
  width: 100%;
  background-color: var(--bg);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
}

.divider {
  border-bottom: 4px solid var(--divider);
}

.btn-normal {
  background-color: var(--primary) !important;
  border: 0px !important;
  border-radius: 10px !important;
  border-color: var(--primary) !important;
  font-family: 'DMSans-Regular';
  height: 44px;
  flex: 1;
  width: 100%;
}

/* ----------------------Floating-Label---------------------------*/

/****  floating-Lable style start ****/

.floating-form {
  margin-top: 20px;
}

.floating-label {
  position: relative;
  margin-bottom: 20px;
  padding-bottom: 10px;
}

.floating-input, .floating-select {
  font-size: 14px;
  padding: 4px 4px;
  padding-left: 1px;
  display: block;
  width: 100%;
  height: 30px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--divider);
  color: var(--color-text);
}

.floating-input:focus, .floating-select:focus {
  outline: none;
  caret-color: var(--primary);
  font-family: 'DMSans-Regular';
  border-bottom: 2px solid var(--primary);
}

.floating-label-input {
  color: var(--placeholder);
  font-size: 14px;
  font-weight: normal;
  font-family: 'DMSans-Regular';
  position: absolute;
  pointer-events: none;
  left: 0px;
  bottom: 14px;
  transition: 0.2s ease all;
  -moz-transition: 0.2s ease all;
  -webkit-transition: 0.2s ease all;
}

/* ----------right-icon-------------*/

.i {
  position: absolute;
  right: 5px;
  top: 5px;
  opacity: 0.7;
  color: var(--primary);
  font-size: 20px;
}

.floating-input:focus~.floating-label-input, .floating-input:not(:placeholder-shown)~.floating-label-input {
  top: -18px;
  font-size: 14px;
  color: var(--primary);
  font-family: 'DMSans-Regular';
}

.floating-select:focus~.floating-label-input, .floating-select:not([value=""]):valid~.floating-label-input {
  top: -18px;
  font-size: 14px;
  color: var(--primary);
  font-family: 'DMSans-Regular';
}

/* active state */

.floating-input:focus~.bar:before, .floating-input:focus~.bar:after, .floating-select:focus~.bar:before, .floating-select:focus~.bar:after {
  width: 50%;
}

*, *:before, *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-family: 'DMSans-Regular';
}

.floating-textarea {
  min-height: 30px;
  max-height: 260px;
  overflow: hidden;
  overflow-x: hidden;
  font-family: 'DMSans-Regular';
}

/* highlighter */

.highlight {
  position: absolute;
  height: 50%;
  width: 100%;
  top: 15%;
  left: 0;
  pointer-events: none;
  opacity: 0.5;
  font-family: 'DMSans-Regular';
}

